@import "utils";

@import "./blocks/pages.scss";
@import "./blocks/container";

$headfoot-color-text: #FFFFFF;


.header {
  padding-top:18px ;
  height: 100vh;
  background-color: hsla(0, 0%, 17%, 1);
  color: $headfoot-color-text;
  

  &__top {
    display: flex;
    justify-content: space-between;
    margin: 0 0 50px;

    @include onTablet640px {
      margin-bottom: 58px;
    }
  }
  
  &__left {
    display: flex;
    gap:36px;
    align-items: center;
    

  }

  &__menu-opener {
    display: block;

    @include onSmallDesktop1024{
      display: none;
    }
  }

  &__menu-list{
    display: none;

    @include onSmallDesktop1024 {
      display: block;
    }


  }


  &__hire-link {
    
    position: relative;
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 700;
    font-size: 13px;
    line-height: 150%;
    text-align: center;
    letter-spacing: 3px;
    text-transform: uppercase;
    color: $headfoot-color-text;
    text-decoration: none;

    &::after {
      content: '';
      position: absolute;
      width: 100%;
      height: 2px;
      background: #2060F6;
      border-radius: 4px;
      bottom:0;
      left:0
    }
  }

  &__middle {
    margin: 0 0 48px;


    @include onTablet640px {
      margin-bottom: 148px;
    }
    

  }

  &__title {
    
    margin: 0 0 32px;
    padding: 0;
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 600;
    font-size: 52px;
    line-height: 120%;
    letter-spacing: -2px;

    @include onTablet640px {
      font-size: 64px;
      line-height: 106%;
      margin-bottom: 24px;
    }

    @include onSmallDesktop1024 {
      margin-bottom: 32px;
    }
  
  }

  &__title-span {
    display: block;

    @include onTablet640px {
      display: inline;
    }

  }

  &__description {
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 150%;
    // align-self: stretch;
    width: 280px;
    margin: 0 0 72px;
    
    @include onTablet640px{
      width:360px;
      margin-bottom: 56px;

    }

    @include onSmallDesktop1024 {
      margin-bottom: 83px;
    }

  }


  &__button  {

  }
}


.logo {
  display:block;
  
 
  
  &__image{
    width: 45px;
    height: 25px;
    
  }

}

.menu {

  &__list {
    display: flex;
    margin: 0;
    padding: 0;
    list-style: none;
    gap:48px;
  }

  &__item {
    
  }

  &__link {
    text-decoration: none;
    text-transform: uppercase;
    color: $headfoot-color-text;
    font-weight: 700;
    font-size: 13px;
    line-height: 150%;
    text-align: center;
    letter-spacing: 3px;

  }
}

.icon {
  width: 18px;
  height: 8px;
  background-image: url(../images/icon/Vector.svg);
  
}

.button{
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #2060F6;
  height: 50px;
  width: 100%;
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 600;
  font-size: 15px;
  line-height: 22px;
/* identical to box height */
  text-decoration: none;
  text-align: center;
  color:$headfoot-color-text;
  border-radius: 8px;
  

  @include onTablet640px {
    width:264px
  }
}

