@mixin hover($_property, $_toValue) {
  transition: #{$_property} 0.3s;
  &:hover {
    #{$_property}: $_toValue;
  }
}


@mixin onTablet640px {
	@media(min-width: 640px){
		@content;
	}
}
@mixin onSmallDesktop1024 {
	@media(min-width: 1024px){
		@content;
	}
}
@mixin onDesktop1280 {
	@media(min-width: 1280px){
		@content;
	}
}
@mixin on1600 {
	@media(min-width: 1600px){
		@content;
	}
}
@mixin largeDisplay2560 {
	@media(min-width: 1600px){
		@content;
	}
}